import React from 'react'
import logo from './phma-logo.png'
import coffee from './coffee.jpg'
const data = require('./UserData')
export const Forms = () => {
    return (
        <div className='verify'>
            <div className='verify-wrapper'>
                <div className='main-container'>
                    <div className='verify-wrapper'>
                        <div className='front-view'>
                            <div className='logo-wrap'>
                                <img src={logo} alt="" className='phma-logo' />
                            </div>
                            <div className='user-image'>
                                <img src={coffee} alt="" className='user' />
                            </div>
                            <div className='user-name'>SAMUEL OWUSU</div>
                            <div className='user-id'>ASSISTANT PROGRAMMER</div>
                            <div className='id-wrap'>
                                <div className='verify-id-number'>ID: 1234567890</div>
                            </div>
                        </div>
                    </div>


                </div>
            </div>
        </div>
    )
}
